.main-nav {
  width: 100%;
  height: 10rem;
  padding: 0 1.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between; }
  @media (min-width: 480px) {
    .main-nav {
      padding: 0 2.5rem; } }
  .main-nav .hamburger-menu {
    height: 5rem;
    width: 5rem;
    background-color: #eee;
    border-radius: 0.4rem;
    box-shadow: 0.5rem 0.5rem 1rem #cacaca, -0.5rem -0.5rem 1rem #fff;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2000; }
  .main-nav .hamburger-menu:hover {
    box-shadow: inset 0.5rem 0.5rem 1rem #cacaca, inset -0.5rem -0.5rem 1rem #fff; }
  .main-nav .bar {
    width: 3rem;
    height: 2px;
    background-color: #333;
    display: inline-block;
    position: relative;
    transition: background-color 0.5s; }
    .main-nav .bar::before, .main-nav .bar::after {
      content: "";
      position: absolute;
      width: inherit;
      height: inherit;
      background-color: #333;
      transition: all 0.5s; }
    .main-nav .bar::before {
      top: -0.8rem; }
    .main-nav .bar::after {
      top: 0.8rem; }

.open .bar {
  background-color: transparent; }
  .open .bar::before {
    top: 0;
    transform: rotate(135deg); }
  .open .bar::after {
    top: 0;
    transform: rotate(-135deg); }

.nav-list {
  width: 30rem;
  height: 100vh;
  margin: 0;
  position: absolute;
  top: 0;
  right: 0;
  transform: translateX(55rem);
