  z-index: 1500;
  transition: transform 0.5s; }
  @media (min-width: 480px) {
    .nav-list {
      width: 45rem; } }

.open .nav-list {
  transform: translateX(0); }

.nav-item {
  border-bottom: 2px solid #16c79a; }

.nav-link {
