.main-heading {
  color: #fff;
  text-shadow: 1px 1px 1rem rgba(0, 0, 0, 0.5);
  margin-bottom: 2.5rem; }
  .main-heading .title {
    font-size: 3.8rem;
    text-transform: uppercase;
    letter-spacing: 3px;
    color: #fff; }
    @media (min-width: 1023px) {
      .main-heading .title {
        font-size: 5.5rem; } }
    .main-heading .title span {
      margin: 0;
      padding: 0;
      position: relative;
      font-size: 4.8rem;
      text-transform: uppercase;
      letter-spacing: 3px;
      color: rgba(248, 241, 241, 0.5);
      -webkit-text-stroke: 0.2vw #00c4a1;
      display: inline-block; }
      @media (min-width: 1023px) {
        .main-heading .title span {
          font-size: 7.5rem;
          -webkit-text-stroke: 0.1vw #00c4a1; } }
      .main-heading .title span::before {
        content: attr(data-text);
        position: absolute;
        top: 0;
        left: 0;
        width: 0;
        height: 100%;
        color: #11998e;
        -webkit-text-stroke: 0vw #00c4a1;
