  width: 100%;
  height: 1rem;
  background-color: var(--gray-color); }

progress::-webkit-progress-bar {
  background-color: var(--gray-color); }

progress::-webkit-progress-value {
  background-color: var(--gray-dark-color); }

progress::-moz-progress-bar {
