.main-heading {
  color: #fff;
  // line-height: 1;
  text-shadow: 1px 1px 1rem rgba(0, 0, 0, 0.5);
  margin-bottom: 2.5rem;

  .title {
    font-size: 3.8rem;
    text-transform: uppercase;
    letter-spacing: 3px;
    color: #fff;

    @include desktop {
      font-size: 5.5rem;
    }

    span {
      margin: 0;
      padding: 0;
      position: relative;
      font-size: 4.8rem;
      text-transform: uppercase;
      letter-spacing: 3px;
      color: rgba(248, 241, 241, 0.5);
      -webkit-text-stroke: 0.2vw #00c4a1;
      display: inline-block;

      @include desktop {
        font-size: 7.5rem;
        -webkit-text-stroke: 0.1vw #00c4a1;
      }

      &::before {
        content: attr(data-text);
        position: absolute;
        top: 0;
        left: 0;
        width: 0;
        height: 100%;
        color: #11998e;
        -webkit-text-stroke: 0vw #00c4a1;
        border-right: 2px solid #00c4a1;
        overflow: hidden;
        animation: animate 6s linear infinite;
      }
    }
  }
  .subtitle {
    font-size: 3.6rem;
    font-weight: 300;
    font-family: "Courgette", cursive;
  }
}

@keyframes animate {
  0%,
  10%,
  100% {
    width: 0;
  }
  70%,
  90% {
    width: 100%;
  }
}
